"use client";

import { useMemo, useState } from "react";

type Engine = "midjourney" | "flux_replicate" | "flux_pro";

type GenerateRequest = {
  topic: string;
  recipe_count: number;
  image_count: number;
  image_engine: Engine;
  style_ref_url: string | null;
  prompt_override: string | null;
  output: { aspect_ratio: string; image_format: string };
  client: { email: string | null };
};

type GenerateResponse = {
  request_id: string;
  status: "done" | "error";
  topic?: string;
  recipe_count?: number;
  image_count?: number;
  files?: {
    article: { name: string; download_url: string } | null;
    album_zip: { name: string; download_url: string } | null;
  };
  error?: { code: string; message: string };
};

function cx(...c: Array<string | false | undefined>) {
  return c.filter(Boolean).join(" ");
}

function Badge({ children }: { children: React.ReactNode }) {
  return (
    <span className="inline-flex items-center rounded-full border border-white/10 bg-white/5 px-2.5 py-1 text-xs text-white/80">
      {children}
    </span>
  );
}

function Field({ label, hint, children }: { label: string; hint?: string; children: React.ReactNode }) {
  return (
    <label className="block">
      <div className="flex items-baseline justify-between">
        <span className="text-sm font-medium text-white/90">{label}</span>
        {hint ? <span className="text-xs text-white/50">{hint}</span> : null}
      </div>
      <div className="mt-2">{children}</div>
    </label>
  );
}

export default function Page() {
  const [topic, setTopic] = useState("Top 3 Recettes Curry");
  const [recipeCount, setRecipeCount] = useState(3);
  const [imageCount, setImageCount] = useState(3);
  const [engine, setEngine] = useState<Engine>("flux_replicate");
  const [styleRefUrl, setStyleRefUrl] = useState("");
  const [promptOverride, setPromptOverride] = useState("");
  const [aspectRatio, setAspectRatio] = useState("3:2");
  const [imageFormat, setImageFormat] = useState("jpg");
  const [email, setEmail] = useState("");

  const [advanced, setAdvanced] = useState(false);
  const [loading, setLoading] = useState(false);
  const [resp, setResp] = useState<GenerateResponse | null>(null);
  const [err, setErr] = useState<string | null>(null);

  const canSubmit = useMemo(() => topic.trim().length > 0 && recipeCount >= 1 && imageCount >= 0, [topic, recipeCount, imageCount]);

  async function onGenerate() {
    setErr(null);
    setResp(null);

    const payload: GenerateRequest = {
      topic: topic.trim(),
      recipe_count: Number(recipeCount),
      image_count: Number(imageCount),
      image_engine: engine,
      style_ref_url: styleRefUrl.trim() ? styleRefUrl.trim() : null,
      prompt_override: promptOverride.trim() ? promptOverride.trim() : null,
      output: { aspect_ratio: aspectRatio.trim() || "3:2", image_format: imageFormat },
      client: { email: email.trim() ? email.trim() : null },
    };

    setLoading(true);
    try {
      const r = await fetch("/api/generate", {
        method: "POST",
        headers: { "Content-Type": "application/json" },
        body: JSON.stringify(payload),
      });

      const data = (await r.json()) as GenerateResponse;

      if (!r.ok) {
        setErr(`HTTP ${r.status}: ${JSON.stringify(data)}`);
        return;
      }
      setResp(data);
    } catch (e: any) {
      setErr(String(e?.message || e));
    } finally {
      setLoading(false);
    }
  }

  return (
    <div className="min-h-screen bg-[#07070A] text-white">
      {/* background glow */}
      <div className="pointer-events-none fixed inset-0 overflow-hidden">
        <div className="absolute -top-40 left-1/2 h-[520px] w-[520px] -translate-x-1/2 rounded-full bg-white/10 blur-3xl" />
        <div className="absolute top-24 left-1/3 h-[420px] w-[420px] rounded-full bg-fuchsia-500/10 blur-3xl" />
        <div className="absolute top-36 right-1/4 h-[420px] w-[420px] rounded-full bg-cyan-500/10 blur-3xl" />
      </div>

      <main className="relative mx-auto w-full max-w-5xl px-6 py-10">
        <header className="flex flex-col gap-4 md:flex-row md:items-end md:justify-between">
          <div>
            <div className="flex items-center gap-2">
              <Badge>n8n hidden</Badge>
              <Badge>Next.js UI</Badge>
              <Badge>Download ready</Badge>
            </div>
            <h1 className="mt-4 text-3xl font-semibold tracking-tight md:text-4xl">
              Recipe Generator
              <span className="text-white/60"> — client console</span>
            </h1>
            <p className="mt-2 max-w-2xl text-sm text-white/60">
              Génère des articles + albums ZIP via workflow n8n. L’interface appelle uniquement <span className="text-white/80">/api/*</span>.
            </p>
          </div>

          <div className="flex items-center gap-3">
            <button
              onClick={() => setAdvanced((v) => !v)}
              className={cx(
                "rounded-xl border px-4 py-2 text-sm transition",
                advanced ? "border-white/20 bg-white/10" : "border-white/10 bg-white/5 hover:bg-white/10"
              )}
            >
              {advanced ? "Hide" : "Show"} advanced
            </button>

            <button
              disabled={!canSubmit || loading}
              onClick={onGenerate}
              className={cx(
                "rounded-xl px-5 py-2.5 text-sm font-medium transition",
                (!canSubmit || loading) ? "bg-white/10 text-white/40" : "bg-white text-black hover:opacity-90"
              )}
            >
              {loading ? "Generating…" : "Generate"}
            </button>
          </div>
        </header>

        <section className="mt-8 grid gap-6 md:grid-cols-2">
          <div className="rounded-2xl border border-white/10 bg-white/[0.03] p-6 shadow-[0_0_0_1px_rgba(255,255,255,0.03)_inset]">
            <h2 className="text-base font-semibold">Inputs</h2>
            <p className="mt-1 text-sm text-white/60">Saisie rapide. Options avancées repliables.</p>

            <div className="mt-5 grid gap-4">
              <Field label="Topic" hint="Markdown title / brief">
                <textarea
                  value={topic}
                  onChange={(e) => setTopic(e.target.value)}
                  rows={5}
                  className="w-full rounded-xl border border-white/10 bg-black/20 px-4 py-3 text-sm text-white/90 outline-none ring-0 placeholder:text-white/30 focus:border-white/25"
                  placeholder="Top 15 recettes…"
                />
              </Field>

              <div className="grid grid-cols-2 gap-3">
                <Field label="recipe_count">
                  <input
                    type="number"
                    min={1}
                    value={recipeCount}
                    onChange={(e) => setRecipeCount(Number(e.target.value))}
                    className="w-full rounded-xl border border-white/10 bg-black/20 px-4 py-3 text-sm outline-none focus:border-white/25"
                  />
                </Field>

                <Field label="image_count">
                  <input
                    type="number"
                    min={0}
                    value={imageCount}
                    onChange={(e) => setImageCount(Number(e.target.value))}
                    className="w-full rounded-xl border border-white/10 bg-black/20 px-4 py-3 text-sm outline-none focus:border-white/25"
                  />
                </Field>
              </div>

              <Field label="image_engine">
                <select
                  value={engine}
                  onChange={(e) => setEngine(e.target.value as Engine)}
                  className="w-full rounded-xl border border-white/10 bg-black/20 px-4 py-3 text-sm outline-none focus:border-white/25"
                >
                  <option value="flux_replicate">flux_replicate</option>
                  <option value="flux_pro">flux_pro</option>
                  <option value="midjourney">midjourney</option>
                </select>
              </Field>

              {advanced && (
                <div className="mt-2 grid gap-4 rounded-2xl border border-white/10 bg-white/[0.02] p-4">
                  <Field label="style_ref_url" hint="optional">
                    <input
                      value={styleRefUrl}
                      onChange={(e) => setStyleRefUrl(e.target.value)}
                      className="w-full rounded-xl border border-white/10 bg-black/20 px-4 py-3 text-sm outline-none focus:border-white/25"
                      placeholder="https://..."
                    />
                  </Field>

                  <Field label="prompt_override" hint="optional">
                    <textarea
                      value={promptOverride}
                      onChange={(e) => setPromptOverride(e.target.value)}
                      rows={4}
                      className="w-full rounded-xl border border-white/10 bg-black/20 px-4 py-3 text-sm outline-none focus:border-white/25"
                      placeholder="Style photo studio, lumière douce…"
                    />
                  </Field>

                  <div className="grid grid-cols-2 gap-3">
                    <Field label="aspect_ratio">
                      <input
                        value={aspectRatio}
                        onChange={(e) => setAspectRatio(e.target.value)}
                        className="w-full rounded-xl border border-white/10 bg-black/20 px-4 py-3 text-sm outline-none focus:border-white/25"
                        placeholder="3:2"
                      />
                    </Field>

                    <Field label="image_format">
                      <select
                        value={imageFormat}
                        onChange={(e) => setImageFormat(e.target.value)}
                        className="w-full rounded-xl border border-white/10 bg-black/20 px-4 py-3 text-sm outline-none focus:border-white/25"
                      >
                        <option value="jpg">jpg</option>
                        <option value="png">png</option>
                        <option value="webp">webp</option>
                      </select>
                    </Field>
                  </div>

                  <Field label="client.email" hint="optional">
                    <input
                      value={email}
                      onChange={(e) => setEmail(e.target.value)}
                      className="w-full rounded-xl border border-white/10 bg-black/20 px-4 py-3 text-sm outline-none focus:border-white/25"
                      placeholder="client@email.com"
                    />
                  </Field>
                </div>
              )}
            </div>

            <div className="mt-5 text-xs text-white/50">
              Tip: la génération peut prendre plusieurs minutes selon le moteur d’image.
            </div>
          </div>

          <div className="rounded-2xl border border-white/10 bg-white/[0.03] p-6 shadow-[0_0_0_1px_rgba(255,255,255,0.03)_inset]">
            <h2 className="text-base font-semibold">Result</h2>
            <p className="mt-1 text-sm text-white/60">Télécharge l’article et l’album ZIP dès que c’est prêt.</p>

            {!resp && !err && (
              <div className="mt-6 rounded-2xl border border-white/10 bg-black/20 p-5 text-sm text-white/60">
                Lance une génération pour voir apparaître les liens.
              </div>
            )}

            {err && (
              <div className="mt-6 rounded-2xl border border-red-500/30 bg-red-500/10 p-5 text-sm text-red-200">
                {err}
              </div>
            )}

            {resp && (
              <div className="mt-6 space-y-4">
                <div className="flex flex-wrap items-center gap-2">
                  <Badge>Status: {resp.status}</Badge>
                  <Badge>Request: {resp.request_id}</Badge>
                  {resp.topic ? <Badge>Topic: {resp.topic}</Badge> : null}
                </div>

                <div className="grid gap-3">
                  {resp.files?.article?.download_url && (
                    <a
                      href={resp.files.article.download_url}
                      className="group flex items-center justify-between rounded-2xl border border-white/10 bg-black/20 px-4 py-4 text-sm hover:border-white/20 hover:bg-black/30"
                    >
                      <div>
                        <div className="font-medium">Download article</div>
                        <div className="mt-1 text-xs text-white/50">{resp.files.article.name}</div>
                      </div>
                      <span className="text-white/60 group-hover:text-white">→</span>
                    </a>
                  )}

                  {resp.files?.album_zip?.download_url && (
                    <a
                      href={resp.files.album_zip.download_url}
                      className="group flex items-center justify-between rounded-2xl border border-white/10 bg-black/20 px-4 py-4 text-sm hover:border-white/20 hover:bg-black/30"
                    >
                      <div>
                        <div className="font-medium">Download album (zip)</div>
                        <div className="mt-1 text-xs text-white/50">{resp.files.album_zip.name}</div>
                      </div>
                      <span className="text-white/60 group-hover:text-white">→</span>
                    </a>
                  )}
                </div>

                <details className="rounded-2xl border border-white/10 bg-black/20 p-4">
                  <summary className="cursor-pointer text-sm text-white/70">Raw JSON</summary>
                  <pre className="mt-3 overflow-auto text-xs text-white/60">{JSON.stringify(resp, null, 2)}</pre>
                </details>
              </div>
            )}
          </div>
        </section>

        <footer className="mt-10 text-xs text-white/40">
          Powered by Next.js + n8n. Downloads are proxied through this app (n8n stays private).
        </footer>
      </main>
    </div>
  );
}
